.servicio {
    display: grid;
    padding: 20px;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    background: linear-gradient(to bottom, rgb(216, 216, 216) 0%, rgb(255, 255, 255) 100%);
    grid-template-areas:
    "h1ser"
    "paraser"
    ".";
}

.servicio .inside {
    display: flex;
    grid-area: paraser;
    flex-direction: column;
    justify-self: center;
}

.servicio h1 {
    grid-area: h1ser;
    justify-self: flex-end;
    height: 50px;
}

.servicio p {
    margin-bottom: 20px;
    font-size: 24px;
    width: 600px;
}

@media (max-width: 767px) {
    .servicio {
        display: grid;
        padding: 20px;
        justify-content: center;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        background: linear-gradient(to bottom, rgb(216, 216, 216) 0%, rgb(255, 255, 255) 100%);
        grid-template-areas:
        "h1ser"
        "paraser";
        height: 100vh;
    }

    .servicio h1 {
        display: flex;
        grid-area: h1ser;
        margin: 0;
        justify-self: center;
        align-self: center;
    }

    .servicio p {
        grid-area: paraser;
        margin: 0;
        font-size: 24px;
        width: 300px;
        margin-top: 20px;
        justify-self: center;
        align-self: flex-start;
    }
}

@media (max-width: 767px) and (orientation: landscape) { 

    .servicio p {
        grid-area: paraser;
        margin: 0;
        font-size: 20px;
        width: 500px;
        justify-self: center;
        align-self: flex-start;
        height: 100vh;
        padding: 200px 0px 0px 0px;
    }
}
