.flecha {
    position: fixed;
    top: 50%;
    right: 0;
    color: white;
}

.flecha img {
    width: 70%;
    height: auto;
    cursor: pointer;
}

.flecha img:hover {
    filter: drop-shadow(0 0 0 black);
}

.flecha .language {
    text-shadow: 2px 2px 2px rgb(0, 56, 3);
    cursor: pointer;
}

.flecha .language:hover {
    text-shadow: 2px 2px 2px rgb(187, 255, 207);
}

@media (max-width: 767px) {
    .flecha {
        display: none;
    }
}