.nosotros {
    display: grid;
    background-color: #393737;
    justify-content: center;
}

.nosotros h1 {
    color: white;
    font-size: 34px;
    height: auto;
    color: white;
    align-self: flex-end;
}

.nosotros p {
    color: white;
    font-size: 24px;
    align-self: center;
    width: 600px;
}

.nosotros a {
    color: green;
    text-decoration: none;
}


@media (max-width: 767px) {
    .nosotros {
        display: grid;
        background-color: #393737;
        justify-content: center;
        height: 100vh;
    }

    .nosotros h1 {
        color: white;
        height: auto;
        color: white;
        align-self: flex-end;
    }

    .nosotros p {
        color: white;
        width: 300px;
        align-self: center;
    }

    .nosotros a {
        color: green;
        text-decoration: none;
    }
}

@media (max-width: 767px) and (orientation: landscape) { 
    .nosotros p {
        color: white;
        width: 500px;
        align-self: center;
    }
 }
