.calidad {
    display: grid;
    padding: 20px;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: 3fr auto 3fr;
    background-color: rgb(120, 145, 119);
    grid-template-areas:
    "imgcal"
    "h1cal"
    "paracal";
}

.calidad img {
    grid-area: imgcal;
    align-self: flex-end;
    width: 10%;
    height: auto;
    justify-self: center;
}

.calidad h1 {
    display: flex;
    grid-area: h1cal;
    justify-self: center;
    align-items: center;
    color: white;
}

.calidad p {
    grid-area: paracal;
    font-size: 24px;
    width: 600px;
    margin-top: 14px;
    justify-self: center;
    align-self: flex-start;
    color: white;
}

.calidad a {
    color: rgb(0, 206, 0);
    text-decoration: none;
}

@media (max-width: 767px) {
    .calidad {
        display: grid;
        padding: 20px;
        justify-content: center;
        grid-template-columns: 1fr;
        grid-template-rows: 3fr 1fr 3fr;
        background-color: rgb(95, 95, 95);
        grid-template-areas:
        "imgcal"
        "h1cal"
        "paracal";
        height: 100vh;
    }

    .calidad img {
        grid-area: imgcal;
        align-self: flex-end;
        width: 30%;
        height: auto;
        justify-self: center;
    }

    .calidad h1 {
        display: flex;
        grid-area: h1cal;
        margin: 0;
        justify-self: center;
        color: white;
    }

    .calidad p {
        grid-area: paracal;
        margin: 0;
        font-size: 24px;
        width: 300px;
        justify-self: center;
        align-self: flex-start;
        color: white;
    }

    .calidad a {
        color: green;
        text-decoration: none;
    }
}


@media (max-width: 767px) and (orientation: landscape) { 
    .calidad img {
        grid-area: imgcal;
        align-self: flex-end;
        width: 20%;
        height: auto;
        justify-self: center;
    }

    .calidad p {
        grid-area: paracal;
        margin: 0;
        font-size: 20px;
        width: 500px;
        justify-self: center;
        align-self: flex-start;
        color: white;
    }
}