.home {
    display: grid;
    min-height: 100%;
    background-size: cover;
    justify-content: center;
    grid-template-columns: auto;
    grid-template-rows: repeat(3, 1fr);
    grid-template-areas: 
    "."
    "logo"
    ".";
}

.home h1 {
    margin: 0 0 10 0;
    color: white;
    font-size: 26px;
    height: auto;
}

.home img {
    width: 25%;
    height: auto;
    margin-top: -120px;
    margin-bottom: 120px;
}

.logo-header {
    grid-area: logo;
    align-self: center;
    padding: 10px;
}

@media (max-width: 767px) {
    .home {
        display: grid;
        width: 100%;
        background-size: cover;
        justify-content: center;
        grid-template-columns: auto;
        grid-template-rows: repeat(3, 1fr);
        grid-template-areas: 
        "."
        "logo"
        ".";
    }

    .home h1 {
        color: white;
        font-size: 26px;
    }

    .home img {
        width: 25%;
        height: auto;
        margin-top: 0px;
        margin-bottom: 0px;
    }

    .logo-header {
        grid-area: logo;
        align-self: center;
    }
}

@media (width: 812px) and (orientation: landscape) { 
    .home img {
        width: 25%;
        height: auto;
        margin-top: 0;
        margin-bottom: 0;
    }
}