.sostenibilidad {
    display: grid;
    padding: 20px;
    justify-content: center;
    background: linear-gradient(to bottom, rgb(216, 216, 216) 0%, rgb(255, 255, 255) 100%);
    grid-template-columns: 1fr;
    grid-template-rows: 3fr auto 3fr;
    grid-template-areas: 
    "imgsos"
    "h1sos"
    "parasos"
}

.sostenibilidad img {
    grid-area: imgsos;
    width: 20%;
    height: auto;
    margin-left: 35px;
    align-self: flex-end;
    justify-self: center;
}

.sostenibilidad h1 {
    display: flex;
    grid-area: h1sos;
    justify-self: center;
    align-items: center;
}

.sostenibilidad p {
    grid-area: parasos;
    font-size: 24px;
    width: 600px;
    margin-top: 14px;
    justify-self: center;
    align-self: flex-start;
}
@media (max-width: 767px) and (orientation: portrait) {
    .sostenibilidad {
        display: grid;
        padding: 20px;
        justify-content: center;
        background: linear-gradient(to bottom, rgb(216, 216, 216) 0%, rgb(255, 255, 255) 100%);
        grid-template-columns: 1fr;
        grid-template-rows: 3fr 1fr 3fr;
        grid-template-areas: 
        "imgsos"
        "h1sos"
        "parasos";
        height: 100vh;
    }

    .sostenibilidad img {
        grid-area: imgsos;
        width: 50%;
        height: auto;
        margin-right: 10px;
        align-self: flex-end;
        justify-self: center;
    }

    .sostenibilidad h1 {
        display: flex;
        grid-area: h1sos;
        margin: 0;
        justify-self: center;
    }

    .sostenibilidad p {
        grid-area: parasos;
        margin: 0;
        font-size: 20px;
        width: 300px;
        justify-self: center;
        align-self: flex-start;
    }
}


@media (max-width: 767px) and (orientation: landscape) {

    .sostenibilidad img {
        grid-area: imgsos;
        width: 25%;
        height: auto;
        margin-right: 10px;
        align-self: flex-end;
        justify-self: center;
        height: 100vh;
    }

    .sostenibilidad p {
        grid-area: parasos;
        margin: 0;
        font-size: 20px;
        width: 500px;
        justify-self: center;
        align-self: flex-start;
    }
}