.soluciones {
    display: grid;
    padding: 20px;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: 3fr auto 3fr;
    background-color: rgb(103, 138, 107);
    grid-template-areas:
    "imgsol"
    "h1sol"
    "parasol";
}

.soluciones img {
    grid-area: imgsol;
    width: 10%;
    height: auto;
    align-self: flex-end;
    justify-self: center;
}

.soluciones h1 {
    display: flex;
    grid-area: h1sol;
    justify-self: center;
    align-items: center;
    color: white;
}

.soluciones p {
    grid-area: parasol;
    font-size: 24px;
    width: 600px;
    margin-top: 14px;
    justify-self: center;
    align-self: flex-start;
    color: white;
}

@media (max-width: 767px) {
    .soluciones {
        display: grid;
        padding: 20px;
        justify-content: center;
        grid-template-columns: 1fr;
        grid-template-rows: 3fr 1fr 3fr;
        background-color: rgb(103, 138, 107);
        grid-template-areas:
        "imgsol"
        "h1sol"
        "parasol";
        height: 100vh;
    }

    .soluciones img {
        grid-area: imgsol;
        width: 30%;
        height: auto;
        align-self: flex-end;
        justify-self: center;
    }

    .soluciones h1 {
        display: flex;
        grid-area: h1sol;
        margin: 0;
        justify-self: center;
        color: white;
    }

    .soluciones p {
        grid-area: parasol;
        margin: 0;
        font-size: 24px;
        width: 300px;
        justify-self: center;
        align-self: flex-start;
        color: white;
    }
}

@media (max-width: 767px) and (orientation: landscape) { 
    .soluciones img {
        grid-area: imgsol;
        width: 20%;
        height: auto;
        align-self: flex-end;
        justify-self: center;
        height: 100vh;
    }

    .soluciones p {
        grid-area: parasol;
        margin: 0;
        font-size: 20px;
        width: 500px;
        justify-self: center;
        align-self: flex-start;
        color: white;
    }
}


