.contacto {
    display: grid;
    margin-top: 10px;
    padding: 20px;
    justify-content: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 200px 1fr 150px;
    background: #393737;
    grid-template-areas:
    ". h1con ."
    ". form ."
    ". button .";
    height: 100vh;
} 


.contacto h2 {
    display: flex;
    grid-area: h1con;
    margin: 0;
    height: 20px;
    justify-self: center;
    align-self: center;
    color: white;
}

.contacto .form {
    grid-area: form;
}

.contacto .form form {
    display: grid;
    text-align: left;
    grid-gap: 10px;
    color: white;
}

.contacto .form label {
    display: flex;
    align-items: flex-end;
}

.contacto .button {
    grid-area: button;
}

.contacto .button button {
    border-radius: 9px;
    font-family: "omnesRegular";
    font-weight: 600;
    height: 40px;
    margin-top: 15px;
    color: #000000;
    font-size: 20px;
    background: #fcfeff;
    padding: 10px 48px 10px 48px;
    border: solid #000000 1px;
    text-decoration: none;
}

.contacto .button button:hover {
    background: #d6d6d6;
    text-decoration: none;
}

.spinner {
  width: 40px;
  height: 40px;
  margin: 100px auto;
  background-color: rgb(255, 255, 255);

  border-radius: 100%;  
  -webkit-animation: sk-scaleout 1.0s infinite ease-in-out;
  animation: sk-scaleout 1.0s infinite ease-in-out;
}

@-webkit-keyframes sk-scaleout {
  0% { -webkit-transform: scale(0) }
  100% {
    -webkit-transform: scale(1.0);
    opacity: 0;
  }
}

@keyframes sk-scaleout {
  0% { 
    -webkit-transform: scale(0);
    transform: scale(0);
  } 100% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
    opacity: 0;
  }
}

@media (max-width: 767px) {
    .contacto {
        display: grid;
        margin-top: 10px;
        padding: 80px 20px 20px 20px;
        justify-content: center;
        grid-template-columns: 1fr 5fr 1fr;
        grid-template-rows: 80px auto auto;
        background: #393737;
        grid-template-areas:
        ". h1con ."
        ". form ."
        ". button .";
        height: 100vh;
    } 


    .contacto h2 {
        display: flex;
        grid-area: h1con;
        margin: 0;
        justify-self: center;
        align-items: center;
        color: white;
    }
}

@media (max-width: 767px) and (orientation: landscape) { 
    .contacto {
        height: 200%;
        grid-template-rows: 80px 1fr 30px;
    }
}

@media (width: 812px) and (orientation: landscape) { 
    .contacto {
        height: 200%;
        grid-template-rows: 80px 1fr 120px;
    }
}