.navbar {
    display: grid;
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 160px;
    text-align: right;
    background: -webkit-linear-gradient(top, rgba(62, 62, 62, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
    background: linear-gradient(to bottom, rgba(62, 62, 62, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
    grid-template-columns: 100px 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 
    "logo menu";
}


.navbar ul {
    grid-area: menu;
    justify-self: flex-end;
    display: -webkit-inline-flex;
    display: inline-flex;
    height: 20px;
}

.navbar li {
    padding: 5px;
    margin-left: 5px;
    list-style-type: none;
    color: white;
    height: 10px;
    cursor: pointer;
}

.navbar li a {
    text-decoration: none;
    color: white;
}

.navbar li a:hover {
    color: rgb(157, 202, 157);
}

#img {
    grid-area: logo;
    padding: 5px;
    margin: 5px 0 0 50px;
    width: 8%;
    height: auto;
    -webkit-transition: opacity 0.7s;
    transition: opacity 0.7s;
}

#img.hide {
    opacity: 0;
}

#img.show {
    opacity: 1;
}


@media (max-width: 767px) {
    .navbar {
        display: grid;
        position: fixed;
        height: 100px;
        z-index: 100;
        width: 100%;
        text-align: right;
        background: -webkit-linear-gradient(top, rgba(62, 62, 62, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
        background: linear-gradient(to bottom, rgba(62, 62, 62, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
        grid-template-columns: 100px 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: 
        "logo menu";
        margin: 5px 0px;
    }


    .navbar ul {
        height: 5%;
        grid-area: menu;
        justify-self: flex-end;
    }

    #img {
        grid-area: logo;
        padding: 5px;
        margin: 5px 0 0 20px;
        width: 25%;
        height: auto;
        -webkit-transition: opacity 0.7s;
        transition: opacity 0.7s;
    }

    #img.hide {
        opacity: 0;
    }

    #img.show {
        opacity: 1;
    }

    .nav-icon {
      margin-right: 20px;  
      width: 40px;
    }

    .nav-icon:after, 
    .nav-icon:before, 
    .nav-icon div {
      background-color: #fff;
      border-radius: 3px;
      content: '';
      display: block;
      height: 5px;
      margin: 7px 0;
    }
}
