#sidenav {
  height: 100%; /* Full-height: remove this if you want "auto" height */
  width: 200px; /* Set the width of the sidebar */
  position: fixed; /* Fixed Sidebar (stay in place on scroll) */
  z-index: 300; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0%;
  background-color: #111; /* Black */
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 20px;
  -webkit-transition: -webkit-transform .7s cubic-bezier(0, .52, 0, 1);
  transition: -webkit-transform .7s cubic-bezier(0, .52, 0, 1);
  transition: transform .7s cubic-bezier(0, .52, 0, 1);
  transition: transform .7s cubic-bezier(0, .52, 0, 1), -webkit-transform .7s cubic-bezier(0, .52, 0, 1);
}

#sidenav.hide {
  -webkit-transform: translate3d(-100vw, 0, 0);
          transform: translate3d(-100vw, 0, 0);
}

#sidenav.show {
  -webkit-transform: translate3d(0vw, 0, 0);
          transform: translate3d(0vw, 0, 0);
  overflow: hidden;
}

#sidenav a {
  margin: 0px;
  height: auto;
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
}

#sidenav a:hover {
  color: #f1f1f1;
}

#sidenav img {
    width: 25%;
    margin-left: 25%;
    height: auto;
}
